<template>
  <div class="content-preOrderDetails">
    <headerUsually btnShow="true" titleText="订单详情" :url="url"></headerUsually>
    <div class="content-list">
      <div class="item" v-for="(item, index) in list" :key="index" @click="getTablenoFun(item)">
        <div class>{{item.text}}</div>
        <div class>{{item.value}}</div>
      </div>
    </div>
    <!-- <div class="go-detail" @click="gotoList()">
      <div class="left">订单详情</div>
      <div class="right">
        <div class>已点{{num}}个菜</div>
        <div class="iconfont icon-jiantou1"></div>
      </div>
    </div>-->
    <div class="registration" @click="registrationFun()">登记</div>
  </div>
</template>

<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import { mapState, mapGetters } from "vuex";
export default {
  components: {
    headerUsually
  },
  data() {
    return {
      list: [],
      num: -1,
      url: "",
      tableno: ""
    };
  },
  computed: {
    ...mapState({
      selectedPreOrder: state => state.mobileOrdering.selectedPreOrder,
      entryPreOrderUrl: state => state.mobileOrdering.entryPreOrderUrl
    })
  },
  async created() {
    console.log("this.entryPreOrderUrl:", this.entryPreOrderUrl);
    this.url = this.entryPreOrderUrl;
    this.tableno = this.$route.params.tableno;
    // console.log("url:", this.url);
    let data = {
      params: {
        accnt: this.selectedPreOrder.accnt
      }
    };
    let res = await ajaxGet("/pos/router?method=findRess", data);
    if (res && res.resultCode == 0) {
      let arr = res.data[0];
      this.num = arr.resPluNum;
      this.list = [
        { text: "单号", value: arr.accnt },
        { text: "预订名称", value: arr.resName },
        { text: "营业点", value: arr.pccodeDescript },
        { text: "班别", value: arr.shift },
        { text: "日期", value: arr.resDate },
        {
          text: "桌号",
          value: arr.tableno || this.tableno,
          noTableno: arr.tableno ? false : true
        },
        { text: "电话", value: arr.phone },
        { text: "备注信息", value: arr.info },
        { text: "销售员", value: arr.saleinfo }
      ];
      this.selectedPreOrder.shift = arr.shift;
    }
  },
  methods: {
    getTablenoFun(item) {
      if (item.noTableno) {
        this.$router.push({
          name: "mobileOrderingSelectedTableNum",
          params: {
            fromUrl: "/mobileOrdering/preOrderDetails",
            pccode: this.selectedPreOrder.pccode,
            type: "selectionTable"
          }
        });
      }
    },
    gotoList() {
      if (this.num > 0) {
        this.$router.push("/mobileOrdering/preOrderList");
        // this.$router.push({
        //   name: "mobileOrderingPreOrderList",
        //   params: { url: this.url }
        // });
      }
    },
    async registrationFun() {
      if (
        !(
          this.selectedPreOrder.tableno ||
          this.selectedPreOrder.code ||
          this.tableno
        )
      ) {
        this.msg("请先选桌号！");
        return;
      }
      //console.log("selectedPreOrder:", this.selectedPreOrder);
      let dcgateway = localStorage.getItem("dcgateway")
        ? JSON.parse(localStorage.getItem("dcgateway"))
        : {};
      // console.log(this.selectedPreOrder.shift);
      // console.log(dcgateway.userInfo.shiftDescript);
      if (this.selectedPreOrder.shift != dcgateway.userInfo.shiftDescript) {
        this.msg("当前班次与预订单班次不同，不能登记！");
        return;
      }
      let data = {
        params: {
          accnt: this.selectedPreOrder.accnt,
          pccode: this.selectedPreOrder.pccode,
          tableno:
            this.selectedPreOrder.tableno ||
            this.selectedPreOrder.code ||
            this.tableno
        }
      };
      //console.log("data:", data);
      let res = await ajaxGet("/pos/router?method=resToCheck", data);
      if (res && res.resultCode == 0) {
        this.selectedPreOrder.accnt = res.data.accnt;
        this.$store.commit("clearEntryPreOrderUrl");
        this.$store.commit("sumbitSelectedTableInfo", this.selectedPreOrder);
        this.$router.push("/mobileOrdering/myOrder");
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>

<style lang="less">
.content-preOrderDetails {
  padding-top: 46px;
  .content-list {
    padding-left: 15px;
    background-color: #fff;
    margin-bottom: 10px;
    .item {
      font-size: 14px;
      color: #666666;
      padding: 19px 0;
      padding-right: 15px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #ddd;
    }
    .item:last-child {
      border-bottom: none;
    }
  }
  .go-detail {
    height: 55px;
    line-height: 55px;
    background: #ffffff;
    display: flex;
    justify-content: space-between;
    padding: 0 17px;
    font-size: 14px;
    color: #666666;
    margin-bottom: 20px;
    .right {
      display: flex;
      justify-content: space-between;
    }
  }
  .registration {
    font-size: 18px;
    color: #c4ad68;
    height: 50px;
    line-height: 50px;
    text-align: center;
    background-color: #fff;
  }
}
</style>



